      padding: 0 5px;
      cursor: pointer; }
      nav ul li.active {
        color: black; }

.sideNav {
  background: #f5f5f5;
  max-width: 250px;
  padding: 5px;
  color: black;
  font-size: 1.2rem;
  top: 5px;
  z-index: 999;
  position: fixed; }
  .sideNav ul {
    list-style: none;
    padding: 0;
    margin: 0; }
    .sideNav ul li {
      display: block;
