.tt {
  position: relative;
  border-bottom: 2px solid #ffc600;
  cursor: help; }

.error span.tt {
  border-bottom: 2px solid var(--darkf); }

.tt .ttt {
  visibility: hidden;
  position: absolute;
  text-align: center;
  z-index: 1;
  background-color: #ffc600;
  border: 2px solid black;
  border-radius: 4px;
  padding: 4px;
  width: 40vw;
  /* max-width: fit-content; */
  margin-left: -20vw;
  bottom: 100%;
  left: 50%;
  margin-bottom: 5px;
  opacity: 0;
  transition: opacity 1s; }

.error .tt .ttt {
  background-color: var(--mg); }

.tt:hover .ttt {
  visibility: visible;
  opacity: 1; }

.tt .ttt::after {
  content: " ";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -7px;
  border-style: solid;
  border-width: 7px;
  border-color: black transparent transparent transparent; }

